import axios from 'axios'
import store from '../store/index'
import API from "../config/api"
import CONST from "../config/const"
import VueCookies from 'vue-cookies'
import {Message} from 'element-ui'

/**
 * http request 拦截器
 * 统一注入token
 */
axios.interceptors.request.use(
    config => {
        if (config.url.indexOf(API.SERVER_URL) === 0) {
            let token = store.state.token || VueCookies.get(CONST.AJAX.TOKEN)
            config.headers[CONST.AJAX.TOKEN] = token
        }
        return config
    },
    error => {
        return Promise.reject(error)
    },
)

/**
 * http response 拦截器
 * 统一处理返回的错误消息
 */
axios.interceptors.response.use(
    response => {
        // 当向服务端发送请求时，更新token
        if (response.request.responseURL.indexOf(API.SERVER_URL) === 0) {
            let token = response.headers[CONST.AJAX.TOKEN]
            if (token) {
                store.commit(CONST.MUTATIONS.SET_TOKEN, token);
                VueCookies.set(CONST.AJAX.TOKEN, token)
            }
        }
        if (response.request.responseURL.indexOf(API.WEB_URL) === 0 ||
            response.request.responseURL.indexOf(API.SERVER_URL) === 0) {
            let data = response && response.data
            if (data && data.success === false) {
                // TODO 正式版需释放
                // Message({
                //     type: "error",
                //     dangerouslyUseHTMLString: true,
                //     message: `<strong>错误 ${data.errorCode}</strong> : ${data.message} ${response.request.responseURL}`
                // })
            }
            return data
        }
        return response;
    },
    error => {
        let data = error && error.response && error.response.data
        if (data) {
            // Message({
            //     type: "error",
            //     dangerouslyUseHTMLString: true,
            //     message: data.status ? `<strong>错误 ${data.status}</strong> : ${data.message}` : data
            // })
            return Promise.reject(error.response)
        } else {
            // Message({
            // 	type: "error",
            // 	dangerouslyUseHTMLString: true,
            // 	message: '服务器无响应，请联系管理员。'
            // })
            return Promise.reject()
        }
    },
)

export default axios
