import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import VueCookies from 'vue-cookies'
import CONST from '../config/const'
const Viewport = () => import('@/views/Viewport')
const Login = () => import('@/views/Login')
const Overview = () => import('@/views/Overview')
const Design = () => import('@/views/Design')
const Presentation = () => import('@/views/Presentation')
const Integration = () => import('@/views/Integration')
const IntegrationPresentation = () => import('@/views/IntegrationPresentation')

Vue.use(Router)

const router = new Router({
    /*mode: 'history',*/
    routes: [{
        path: '/',
        name: 'Viewport',
        component: Viewport
    }, {
        path: '/login',
        name: 'Login',
        component: Login
    }, {
        path: '/overview',
        name: 'Overview',
        component: Overview
    }, {
        path: '/design',
        name: 'Design',
        component: Design
    }, {
        path: '/presentation',
        name: 'Presentation',
        component: Presentation
    }, {
        path: '/integration',
        name: 'Integration',
        component: Integration
    }, {
        path: '/integration_presentation',
        name: 'IntegrationPresentation',
        component: IntegrationPresentation
    }]
})

/**
 * 全局路由守护，当token不存在时，跳转到登陆页
 */
router.beforeEach((to, from, next) => {
    if (to.path === "/login" ||
        to.path === "/presentation" ||
        to.path === "/integration_presentation" ||
        to.path === "/slide_presentation") {
        next()
        return
    }
    if (!store.state.token && !VueCookies.isKey(CONST.AJAX.TOKEN)) {
        next("/login")
    } else {
        let cookieToken = VueCookies.get(CONST.AJAX.TOKEN)
        if (cookieToken) {
            store.commit(CONST.MUTATIONS.SET_TOKEN, cookieToken)
        }
        next()
    }
})

export default router
